.section.coming-soon-map .container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    padding: 30px;
    min-height: 100%;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
@media (min-width: $(tablet)) {
    .section.coming-soon-map .container {
        padding: 60px;
    }
}
.section.coming-soon-map .container .text {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    background: $gold;
    padding: 30px;
    height: 100%;
    margin: 0;
    -webkit-box-ordinal-group: 3;
    -webkit-order: 2;
        -ms-flex-order: 2;
            order: 2;
}
@media (min-width: $(desktop)) {
    .section.coming-soon-map .container .text {
        padding: 70px;
        -webkit-box-ordinal-group: 2;
        -webkit-order: 1;
            -ms-flex-order: 1;
                order: 1;
        width: calc(100% / 2);
    }
}
.section.coming-soon-map .container .text .title h3 {
    font-size: 24px;
    font-weight: bold;
    line-height: 1.25;
    color: $white;
    letter-spacing: normal;
}
.section.coming-soon-map .container .text .body {
    margin: 20px 0;
}
.section.coming-soon-map .container .text .body * {
    font-size: 16px;
    font-weight: 600;
    line-height: 1.5;
    font-family: 'Open sans';
}
.section.coming-soon-map .container .text .title h3,
            .section.coming-soon-map .container .text .body * {
    color: $white;
    text-transform: none;
}
.section.coming-soon-map .container .text .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $white;
    color: $gold;
}
.section.coming-soon-map .container .text .button:hover {
    box-shadow: 0 0 0 4px $white,
            inset 0 0 0 3px $gold;
    color: $gold;
}
.section.coming-soon-map .container .google-map-wrapper {
    height: 250px;
    position: relative;
    background: $black;
    padding: 10px;
    border: 1px solid $gold;
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
}
@media (min-width: $(desktop)) {
    .section.coming-soon-map .container .google-map-wrapper {
        -webkit-box-ordinal-group: 3;
        -webkit-order: 2;
            -ms-flex-order: 2;
                order: 2;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        height: auto;
        width: calc(100% / 2);
    }
}
.section.coming-soon-map .container .google-map {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 100%;
    height: 100%;
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
